import React from 'react';
import styles from './MiddleContent.module.css';
import Logo from '../../assets/1Capture.PNG';
import Login from '../Login/Login';
import Register from '../Register/Register'
import ToggleButton from '../ToggleButton/ToggleButton';

const MiddleContent = (props)=>{
    let form = (<Login 
                    loginFormSubmit = {props.loginFormSubmit}
                    loginMessage = {props.loginMessage}
                    />);
    if(props.formType === 'register'){
        form = (<Register 
                    registerFormSubmit = {props.registerFormSubmit}
                    registerMessage = {props.registerMessage}
                    />)
    }
    return(
        <div>
            <ToggleButton loginShow={props.loginShow} registerShow={props.registerShow} formType={props.formType} />
            <div className={styles.MiddleContent}>
                <div className={styles.Container}>
                    <div className={styles.image}>
                        <img src={Logo} alt="Logo" />
                    </div>
                    <div className = {styles.forms}>
                        {/* <Login /> */}
                        {/* <Register /> */}
                        {form}
                    </div>
                </div>
            </div>
        </div>
        
    );
}

export default MiddleContent;