import React from 'react';
import styles from './ToggleButton.module.css';
import Button from '../Button/Button';

const ToggleButton = (props)=>{
    return(
        <div className={styles.Container}>
            <div className={styles.ButtonContainer}>
                <Button onClick={props.loginShow} backColor={(props.formType === 'login') ? 'yellow' : 'white'} color="#0c3d39">Login</Button>
                <Button onClick={props.registerShow} backColor={(props.formType === 'login') ? 'white' : 'yellow'} color="#0c3d39">Register</Button>
            </div>
        </div>
    );
}

export default ToggleButton;