import React,{Component} from 'react';
import './App.module.css';
import Navbar from './components/Navbar/Navbar';
import Footer from './components/Footer/Footer';
import MiddleContent from './components/MiddleContent/MiddleContent';
import axios from 'axios';

class App extends Component{
  state = {
    data : {
      loginEmail : '',
      name : '',
      phone : '',
      email : '',
      password : '',
      city : '',
      country : ''
    },
    formType : 'register',
    loginMessage : '',
    registerMessage : '',
    sideToggle : false
  }

  registerFormShow = ()=>{
      this.setState({formType : 'register'});
  }

  loginFormShow = ()=>{
    this.setState({formType : 'login'});
  }

  loginFormSubmit = (event)=>{
    event.preventDefault();
    axios.post('http://localhost:8000/login' ,{
      email : event.target.loginEmail.value,
      password : event.target.loginPassword.value
    }).then(response=>{
      // console.log(response);
      this.setState({loginMessage : response.data.message})
    }).catch(err=>{
      console.log(err);
    })
  }

  registerFormSubmit = (event)=>{
    event.preventDefault();
    // console.log(event.target);
    axios.post('http://localhost:8000/register' ,{
      name : event.target.name.value,
      phone : event.target.phone.value,
      email : event.target.email.value,
      password : event.target.password.value,
      city : event.target.city.value,
      country : event.target.country.value
    }).then(response=>{
      // console.log(response);
      this.setState({registerMessage : response.data.message})
    }).catch(err=>{
      console.log(err);
    })
  }

  handleChange = (event)=>{
    let data = {...this.state.data,
              [event.target.name] : event.target.value
        }
    this.setState({data : data});
  }

  toggleHandler = ()=>{
    this.setState({sideToggle : !this.state.sideToggle});
  }

  render(){
    return (
      <div className="App">
          <Navbar toggle={this.state.sideToggle} toggleHandler = {this.toggleHandler} />
          <MiddleContent 
            loginShow={this.loginFormShow} 
            registerShow={this.registerFormShow} 
            formType={this.state.formType}
            loginFormSubmit = {this.loginFormSubmit}
            registerFormSubmit = {this.registerFormSubmit}
            loginMessage = {this.state.loginMessage}
            registerMessage = {this.state.registerMessage}
            />
          <Footer />
      </div>
    );
  }
}

export default App;
