import React from 'react';
import styles from './Login.module.css';
import Button from '../Button/Button';

const Login = (props)=>{
    return(
        <div className={styles.Login}>
            <div className={styles.Container}>
                <div className={styles.heading}>
                    <h2>Member Login</h2>
                </div>
                <form onSubmit={props.loginFormSubmit}>
                    <div className={styles.inputBox}>
                        <div className={styles.socialContainer}>
                            <i className='fa fa-lock'></i>
                        </div>
                        <div className={styles.inputContainer}>
                            <input type="email" name='loginEmail' placeholder="Registered Email Id"/>
                        </div>
                    </div>
                    <div className={styles.inputBox}>
                        <div className={styles.socialContainer}>
                            <i className='fa fa-user'></i>
                        </div>
                        <div className={styles.inputContainer}>
                            <input type="password" name='loginPassword' placeholder="Your Password"/>
                        </div>
                    </div>
                    <div className={styles.Error}>
                        <p>{props.loginMessage}</p>
                    </div>
                    <div className={styles.buttonContainer}>
                        <Button type="submit" backColor="#0c3d39" color="white">Login</Button>
                    </div>
                </form>
                    <div className={styles.forgotContainer}>
                        <div className={styles.rememberMe}>
                            <input type="checkbox" />
                            <h6>Remember Me</h6>
                        </div>
                        <h6>Forgot Password</h6>
                    </div>
            </div>
        </div>
    );
}

export default Login;